.nav-wrapper {
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    font-family: 'Tajawal', sans-serif;
}

.nav-wrapper nav {
    display: flex;
    flex-direction: row;

}

.nav-wrapper nav a {
    padding: 5px;
    text-decoration: none;
    color: whitesmoke;
}

.logo {
    padding-left: 10px;
    text-decoration: none;
    color: whitesmoke;
}

.logo:hover {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
}


.about-me {
    display: flex;
    flex-direction: column;
    padding-left: 160px;
    padding-top: 35px;
    font-family: 'Tajawal', sans-serif; 
}

h1 {
    color: whitesmoke;
}

p {
    display: flex;
    flex-wrap: wrap;
    color: whitesmoke;
    width: 50%;
}
:root {
    background-image: url(/portfolio-react/static/media/IMG_3023.94dcdb6f.JPG); 
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    
}
.card {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    width: 180px;
    height: 180px;
    padding:10px
}

.custom-img {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .middle {
    display: flex;
    flex-direction: column;
    transition: .5s ease;
    opacity: 0;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: center;
    visibility: none;
  }

  .card:hover .custom-img {
    opacity: 0.3;
  }
  
  .card:hover .middle {
    opacity: 1;
  }
    
  .text {
    color: whitesmoke;
    font-size: 16px;
    font-family: 'Tajawal', sans-serif; 
  }
.project-card-wrapper {
    display: flex;
    justify-content: center;
    width: 100vw;
    height: auto;
}
.portfolio-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
}
